import { notFound } from 'next/navigation'
import Link from 'next/link'
import { getTenantBySlug, getPublicServices, getPublicEmployees } from '@/lib/actions/public-booking'
import { getPublicLocations } from '@/lib/actions/locations'
import { createClient } from '@/lib/supabase/server'
import { createAdminClient } from '@/lib/supabase/admin'
import { BookingForm } from './booking-form'
import { LocationSelector } from './location-selector'
import { ChatWidget } from '@/components/chat-widget'
import { Sparkles, User, LogOut } from 'lucide-react'
import { Button } from '@/components/ui/button'

type CustomerData = {
  id: string
  firstName: string
  lastName: string
  email: string
  phone: string | null
}

async function getLoggedInCustomer(tenantId: string): Promise<CustomerData | null> {
  const supabase = await createClient()
  const { data: { user } } = await supabase.auth.getUser()

  if (!user || !user.email) {
    return null
  }

  const adminClient = createAdminClient()
  const { data: customer } = await adminClient
    .from('customers')
    .select('id, first_name, last_name, email, phone')
    .eq('tenant_id', tenantId)
    .eq('email', user.email.toLowerCase())
    .single() as { data: { id: string; first_name: string; last_name: string; email: string | null; phone: string | null } | null }

  if (!customer) {
    // User is logged in but not a customer of this tenant yet
    // Return user metadata as fallback
    return {
      id: '',
      firstName: user.user_metadata?.first_name || '',
      lastName: user.user_metadata?.last_name || '',
      email: user.email,
      phone: null,
    }
  }

  return {
    id: customer.id,
    firstName: customer.first_name,
    lastName: customer.last_name,
    email: customer.email || user.email,
    phone: customer.phone,
  }
}

export default async function PublicBookingPage({
  params,
}: {
  params: Promise<{ slug: string }>
}) {
  const { slug } = await params

  const { tenant, error: tenantError } = await getTenantBySlug(slug)

  if (tenantError || !tenant) {
    notFound()
  }

  // Check if tenant has multiple locations
  const { locations } = await getPublicLocations(slug)
  const hasMultipleLocations = locations.length > 1

  // If multiple locations, show location selector
  if (hasMultipleLocations) {
    return (
      <div className="min-h-screen bg-[#0a0a0a] text-white">
        <div className="container mx-auto px-4 py-8 max-w-2xl">
          {/* Header */}
          <header className="text-center mb-8">
            {tenant.logo_url ? (
              <img
                src={tenant.logo_url}
                alt={tenant.name}
                className="h-16 mx-auto mb-4 rounded-lg"
              />
            ) : (
              <div className="w-16 h-16 mx-auto mb-4 rounded-full bg-amber-500/10 flex items-center justify-center">
                <Sparkles className="w-8 h-8 text-amber-400" />
              </div>
            )}
            <h1 className="text-3xl font-bold text-white">{tenant.name}</h1>
            <p className="text-white/60 mt-2">WÃ¤hle einen Standort</p>
          </header>

          {/* Location Selector */}
          <LocationSelector locations={locations} tenantSlug={slug} />

          {/* Footer */}
          <footer className="text-center mt-8 text-sm text-white/40">
            <p>Powered by Esylana</p>
          </footer>
        </div>

        <ChatWidget tenantSlug={slug} tenantName={tenant.name} whatsappNumber={tenant.whatsapp_number || undefined} />
      </div>
    )
  }

  // Single location or no locations - show booking form directly
  const primaryLocation = locations.find(l => l.is_primary) || locations[0]

  const [{ services }, { employees }, customerData] = await Promise.all([
    getPublicServices(tenant.id, primaryLocation?.id),
    getPublicEmployees(tenant.id, primaryLocation?.id),
    getLoggedInCustomer(tenant.id),
  ])

  return (
    <div className="min-h-screen bg-[#0a0a0a] text-white">
      <div className="container mx-auto px-4 py-8 max-w-2xl">
        {/* Header */}
        <header className="text-center mb-8">
          {/* User Actions */}
          <div className="flex justify-end mb-4">
            {customerData ? (
              <div className="flex items-center gap-3">
                <Link href="/customer/termine">
                  <button className="flex items-center px-3 py-2 text-sm text-white/70 hover:text-amber-400 transition-colors">
                    <User className="w-4 h-4 mr-2" />
                    {customerData.firstName || 'Meine Termine'}
                  </button>
                </Link>
                <form action="/api/auth/logout" method="POST">
                  <input type="hidden" name="redirect" value={`/book/${slug}`} />
                  <button type="submit" className="flex items-center px-3 py-2 text-sm border border-white/20 rounded-lg text-white/70 hover:text-amber-400 hover:border-amber-400/50 transition-colors">
                    <LogOut className="w-4 h-4 mr-2" />
                    Abmelden
                  </button>
                </form>
              </div>
            ) : (
              <div className="flex items-center gap-2">
                <Link href={`/customer/login?redirect=/book/${slug}&tenant=${slug}`}>
                  <button className="px-4 py-2 text-sm text-white/70 hover:text-amber-400 transition-colors">
                    Anmelden
                  </button>
                </Link>
                <Link href={`/customer/signup?redirect=/book/${slug}&tenant=${slug}`}>
                  <button className="px-4 py-2 text-sm border border-white/20 rounded-lg text-white/70 hover:text-amber-400 hover:border-amber-400/50 transition-colors">
                    Registrieren
                  </button>
                </Link>
              </div>
            )}
          </div>

          {tenant.logo_url ? (
            <img
              src={tenant.logo_url}
              alt={tenant.name}
              className="h-16 mx-auto mb-4 rounded-lg"
            />
          ) : (
            <div className="w-16 h-16 mx-auto mb-4 rounded-full bg-amber-500/10 flex items-center justify-center">
              <Sparkles className="w-8 h-8 text-amber-400" />
            </div>
          )}
          <h1 className="text-3xl font-bold text-white">{tenant.name}</h1>
          <p className="text-white/60 mt-2">
            {tenant.business_type === 'gastronomy' ? 'Tischreservierung' : 'Online Terminbuchung'}
          </p>
          {primaryLocation && (primaryLocation.address || primaryLocation.city) && (
            <p className="text-sm text-white/40 mt-1">
              {primaryLocation.address}{primaryLocation.address && primaryLocation.city ? ', ' : ''}{primaryLocation.city}
            </p>
          )}
        </header>

        {/* Booking Form */}
        <BookingForm
          tenant={tenant}
          services={services || []}
          employees={employees || []}
          locationId={primaryLocation?.id}
          customerData={customerData}
          businessType={tenant.business_type || 'beauty_clinic'}
        />

        {/* Footer */}
        <footer className="text-center mt-8 text-sm text-white/40">
          <p>Powered by Esylana</p>
        </footer>
      </div>

      {/* Chat Widget with tenant context */}
      <ChatWidget tenantSlug={slug} tenantName={tenant.name} />
    </div>
  )
}
